.logo-size{
  width: 100px;
}


#formul form{
  border: 1px solid #f15b2e;
  padding: 10px; 
  width: 100%;
  font-family: "Roboto";
  font-size: bold;
  border-radius: 5px;
}

#h2 {
  text-align: center;
  background-size:auto ;
  font-family: 'Roboto';
  font-size: 2em ;
  color: #f15b2e;
  width: 100%;
}

p#para{
  font-family: 'Roboto';
  font-size: .8em;
  margin-bottom: .2px;

}
#label {
  font-family: 'Roboto';
  font-size: .9em;
  display: block;
  color: #333;
}

em{
  position: absolute;
  color: #f15b2e;
}

input[type="text"],
input[type="email"],
input[type="tel"] {
  border: 2px solid #f7f7f7;
  font-size: 14px;
  width: 100%;
  height: 35px;
  color: #333;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
}


#horaire{
  padding: 3px 8px;
  border-radius: 6px;
}

select{
  height: 35px;
}

input[type="text"]:hover,
input[type="email"]:hover,
input[type="tel"]:hover{
  border: 2px solid #f15b2e;
  opacity: 50%;
  outline: none;
}

#horaire:hover{
  border: 2px solid #f15b2e;
  opacity: 50%;
  outline: none;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="tel"]:focus{
  border: 2px solid #f15b2e;
  outline: none;
  transform: translateY(-3px);
}

#btn[type="submit"] {
  background-color: #f15b2e;
  color: #fff;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 10px;
  transition: all 0.3s ease-in-out;
}

#btn[type="submit"]:hover {
  background-color: #fff;
  color: #f15b2e;
  border: 1px solid #f15b2e;
  transform: translateY(-3px);
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
}

.tempContDiv{
  margin-top: 20px;
}